	module.exports = {"data":{"markdownRemark":{"html":"<p>Piggybacking on last week’s episode on learning to program, I discuss how we might carry over some of the principles of open source software development as a part of our solution-building toolbox as learning experience designers. I’ve been contributing to an open source project myself for several months now and I’ve started a daily(ish) coding practice on game development and the further along I go into my coding and LX design journey, the more admiration I have for the principles of open source. Wouldn’t it be great for other newbies if we could be more open with each other?</p>\n<iframe src=\"https://simplecast.com/e/47496?style=medium-light\" width=\"100%\" height=\"94px\" frameborder=\"0\" scrolling=\"no\" seamless=\"\"></iframe>\n<p>As always you can get the shownotes on the show page: <a href=\"https://dearinstructionaldesigner.simplecast.fm/\">https://dearinstructionaldesigner.simplecast.fm/</a> I’d love to know what you think. Do you use open source principles in your work?</p>","frontmatter":{"date":"November 10, 2016","path":"/blog/dearid-the-open-source-way","title":"#DearID: The Open Source Way","tags":["dear id","methodology","open source","technology","transparency"],"categories":["Coding","Dear ID Show"],"featured":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":1,"src":"/static/The-Open-Source-Way-9023dc4d58e8f53a2c2396baabcab63d-81d39.png","srcSet":"/static/The-Open-Source-Way-9023dc4d58e8f53a2c2396baabcab63d-9a8d1.png 203w,\n/static/The-Open-Source-Way-9023dc4d58e8f53a2c2396baabcab63d-c53f9.png 405w,\n/static/The-Open-Source-Way-9023dc4d58e8f53a2c2396baabcab63d-81d39.png 810w,\n/static/The-Open-Source-Way-9023dc4d58e8f53a2c2396baabcab63d-e460a.png 1215w,\n/static/The-Open-Source-Way-9023dc4d58e8f53a2c2396baabcab63d-f34da.png 1400w","sizes":"(max-width: 810px) 100vw, 810px"}}}}}},"pathContext":{}}

